import React from "react"
import Bootstrap from '../components/Bootstrap';

class Index extends React.Component {
  render() {
    return (
      <div>
        <Bootstrap />
        <div>
          <h3>Palatalized Initials</h3>
          <table class='table'>
            <thead>
              <tr>
                <th>IPA</th>
                <th>Letter</th>
                <th>Consonantal Class</th>
                <th>Example</th>
                <th>Meaning</th>
              </tr>
            </thead>
            <tbody>
              <tr class="danger">
                <th rowspan='2'>pj</th>
                <th>py</th>
                <th>1U</th>
                <th>pya</th>
                <th>fish</th>
              </tr>
              <tr class="info">
                <th>by</th>
                <th>2</th>
                <th>bya</th>
                <th>stir</th>
              </tr>
              <tr class="warning">
                <th>pʰj</th>
                <th>phy</th>
                <th>1A</th>
                <th>phya</th>
                <th>stone hill</th>
              </tr>
              <tr class="info">
                <th rowspan='2'>mj</th>
                <th>my</th>
                <th>2</th>
                <th>mya</th>
                <th>broken</th>
              </tr>
              <tr class="danger">
                <th>mhy</th>
                <th>1U</th>
                <th>mhyán</th>
                <th>arm wrestling</th>
              </tr>
              <tr class="danger">
                <th rowspan='2'>kj</th>
                <th>ky</th>
                <th>1U</th>
                <th>kya</th>
                <th>to add</th>
              </tr>
              <tr class="info">
                <th>gy</th>
                <th>2</th>
                <th>gya</th>
                <th>tea</th>
              </tr>
              <tr class="warning">
                <th>kʰj</th>
                <th>khy</th>
                <th>1A</th>
                <th>khya</th>
                <th>to look for</th>
              </tr>
              <tr class="info">
                <th rowspan='2'>ŋj (j)</th>
                <th>ngy</th>
                <th>2</th>
                <th>ngya</th>
                <th>tusk</th>
              </tr>
              <tr class="danger">
                <th>nghy</th>
                <th>1U</th>
                <th>nghya</th>
                <th>dense (of vegetation)</th>
              </tr>
            </tbody>
          </table>
        </div>
      </div>
    );
  }
}

export default Index;
